export default {
   created(){
      if (this.is_touch_device()) {
         alert('')
         this.$router.push('/')
      }
   },
   methods:{
      is_touch_device() {
         return (
            "ontouchstart" in window ||
            navigator.MaxTouchPoints > 0 ||
            navigator.msMaxTouchPoints > 0
         );
      },
   }
}